@import url(https://fonts.googleapis.com/css?family=Open+Sans|Overpass+Mono);
/* include the font and colors chosen for the project */

:root {
  --font: "Open Sans", sans-serif;
  --clr-theme: hsl(275, 95%, 49%);
  --clr-theme-drk: hsl(275, 95%, 35%);
  --clr-theme-lgh: hsl(275, 95%, 54%);
  --clr-phone: hsl(0, 0%, 100%);
  --clr-text: hsl(0, 0%, 30%);
  --clr-text-lgh: hsl(0, 0%, 50%);
  --clr-text-bg: hsl(0, 0%, 95%);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  min-height: 100vh;
  /* include the chosen variables for the color of text, background and the font family */
  color: hsl(0, 0%, 30%);
  color: var(--clr-text);
  font-family: "Open Sans", sans-serif;
  font-family: var(--font);
  /* center the content horizontally  */
  display: flex;
  justify-content: center;
  margin: 2.5rem 0;
}
/* wrapping container */
.App {
    /* display the components in a grid of 1 column and 2 rows */
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 90vw;
    max-width: 350px;
    /* keeping the ratio of a normal phone, set the height in relation to the width property */
    height: 144vw;
    max-height: 560px;
    /* include padding for the phone's application */
    padding: 1.5% 2.5% 2.5%;
    /* include rounded corners and a notable shadow beneath the UI */
    box-shadow: 0 0 1vw 0 darkslategray;
    border-radius: 6vw;
    /* position relative to include a couple of visuals to remind of a phone outline */
    position: relative;
  }
.CalculatorDisplay {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    text-align: right;
    border-bottom: .1vh solid darkslategray;
  }
  .CalculatorDisplay h2 {
    font-size: 0.9rem;
  }
  .CalculatorDisplay h1 {
    margin-top: 0.5rem;
    font-size: 1.5rem;
  }
  @media (min-width: 500px) {
    .CalculatorDisplay h2 {
      font-size: 1rem;
    }
    .CalculatorDisplay h1 {
      font-size: 2.2rem;
    }
  }
.CalculatorInput {
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-template-areas: "clear clear divide multiply" "seven eight nine subtract" "four five six add" "one two three equals" "zero zero decimal equals";
  }
  .CalculatorInput button {
    padding: 15% 0;
    color: inherit;
    font-family: inherit;
    background: none;
    border: .1vh solid darkslategray;
    margin: 2.5% 1.5%;
    border-radius: 6vw;
    background-color: white;
    text-transform: uppercase;
    font-size: 1.1rem;
  }
  
  .CalculatorInput button#zero {
    grid-area: zero;
  }
  .CalculatorInput button#one {
    grid-area: one;
  }
  .CalculatorInput button#two {
    grid-area: two;
  }
  .CalculatorInput button#three {
    grid-area: three;
  }
  .CalculatorInput button#four {
    grid-area: four;
  }
  .CalculatorInput button#five {
    grid-area: five;
  }
  .CalculatorInput button#six {
    grid-area: six;
  }
  .CalculatorInput button#seven {
    grid-area: seven;
  }
  .CalculatorInput button#eight {
    grid-area: eight;
  }
  .CalculatorInput button#nine {
    grid-area: nine;
  }
  .CalculatorInput button#add {
    grid-area: add;
  }
  .CalculatorInput button#subtract {
    grid-area: subtract;
  }
  .CalculatorInput button#multiply {
    grid-area: multiply;
  }
  .CalculatorInput button#divide {
    grid-area: divide;
  }
  .CalculatorInput button#decimal {
    grid-area: decimal;
  }
  .CalculatorInput button#equals {
    grid-area: equals;
  }
  .CalculatorInput button#clear {
    grid-area: clear;
  }
  
  /* on hover/focus slightly darken the #fff background */
  .CalculatorInput button:hover,
  .CalculatorInput button:focus {
    background: var(--clr-text-bg);
  }
  /* when clicked, change the background to its initial state */
  .CalculatorInput button:active {
    background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
    background: initial;
  }
